@media screen and (min-width: 1200px){
  .feedback-wrapper{
    position: relative;
    font-size: 16px;
  }

  .feedback {
    width: 100%;
    margin:auto;
    height: 0;
    padding-bottom: 90vh;
    /* max-height: 480px; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
    /* border: solid black 1px; */
  }

  .feedback-background{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .box-review-wrapper {
    width: 30%;
    height: 80%;
    margin-left: 7%;
    padding-top: 3%;
    margin-right: 0px;
    /* display: inline-block; */
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .item-display-rating{
    width: 57%;
    margin-top: 4%;
    margin-left: 38%;
    position: absolute;
    height: 70%;
    background-color: white;
  }

  .item-display-wrapper{
    height: 50%;
  }

  .feedback-rating{
    margin-top: 1%;
    height: 55%;
    background-size: 100% 100%;
  }

}

@media screen and (max-width: 1200px)and (min-width: 415px){
  .feedback-wrapper{
    position: relative;
    font-size: 10px;
  }
  .feedback {
    width: 100%;
    margin:auto;
    height: 0;
    padding-bottom: 40%;
    /* max-height: 480px; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
  }

  .feedback-background{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .box-review-wrapper {
    width: 30%;
    height: 73%;
    margin-left: 7%;
    padding-top: 3%;
    margin-right: 0px;
    /* display: inline-block; */
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .item-display-rating{
    width: 57%;
    margin-top: 4%;
    margin-left: 38%;
    position: absolute;
    height: 50%;
  }

  .item-display-wrapper{
    height: 50%;
  }

  .feedback-rating{
    margin-top: 1%;
    height: 55%;

  }
}
@media screen and (max-width: 414px){
  .feedback{
    width: 90%;
    margin: auto;
    font-size: 10px;
  }
  .item-display-rating{
    width: 100%;
  }

  .feedback-background-wrapper{
    display: none;
  }

}
.Ratingbar-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;

}



